.card > * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  margin: 0;
  padding: 32px 16px 0;
  display: flex;
}

.card__img {
  align-self: center;
  max-width: 160px;
  height: 134px;
}

.card__name {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__review-count {
  font-size: 10px;
  line-height: 14px;
}

.card__price > * {
  margin: 0;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  line-height: 18px;
  display: flex;
}

.card__price-name {
  color: #616070;
  font-size: 12px;
}

.card__price-price {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  height: 40px;
  margin: 16px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all .5s ease-in;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
  transition: all .5s ease-out;
}

.stars, .stars > * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.stars {
  justify-content: left;
  padding-left: 0;
  display: flex;
}

.stars__star {
  box-sizing: border-box;
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  padding: 0;
  list-style: none;
}

.stars__star:last-child {
  margin: 0;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  box-sizing: border-box;
  background-color: #f3f3f3;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}
